.header {
    &-project {
        margin-right: -200px;
        font-family: "Segoe UI";
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        padding: 60px 0 50px;
        position: relative;
        height: 100%;
        width: 830px;
        color: $white;
        clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);

        &__title {
            margin-left: 240px;
            font-weight: 700;
            font-size: 47px; }
        &__text {
            width: 400px;
            font-size: 23px;
            margin-left: 160px;
            span {
                display: block; } }
        &__link {
            padding: 2px;
            z-index: 1;
            color: $white;
            display: inline-block;
            margin-top: 10px;
            font-size: 18px;
            position: relative;
            cursor: pointer;
            text-decoration: none !important;
            transition: all 0.3s ease-out;
            &:hover {
                color: $black; }
            &:hover:before {
                height: 100%; }
            &::before {
                transition: all 0.3s ease-out;
                z-index: -1;
                height: 1px;
                width: 100%;
                background: $white;
                content: '';
                position: absolute;
                bottom: -1px;
                left: 0; }
            &::after {
               content: '\2192';
               position: absolute;
               margin-left: 5px;
               color: $white; } }
        &__img {
            z-index: -1;
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            &::before {
                content: '';
                position: absolute;
                width: 100%;
                height: 50%;
                bottom: 0;
                left: 0;
                background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%); }
            img {
                max-width: 100%; } }
        &s {
            display: none;
            height: 100%;
            padding-top: 120px;
            z-index: 2;
            position: relative;
            padding-bottom: 120px;
            &__container {
                height: 100%; }
            &__title {
                line-height: 1.5;
                position: absolute;
                text-transform: uppercase;
                font-size: 30px;
                font-weight: 900; } } } }

@media (max-width: 768px) {
    .header {
        &-project {
            clip-path: none;
            margin: 0;
            padding: 30px 0;
            height: 100%;
            width: 100%;
            justify-content: start;
            align-items: flex-start;
            &__img {
                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover; } }
            &__title {
                line-height: 1.5;
                max-width: 540px;
                width: 100%;
                margin: 0 auto;
                padding-left: 50px;
                margin-bottom: 50px; }
            &__text {
                max-width: 540px;
                width: 100%;
                margin: 0 auto;
                padding: 0 15px; }
            &s {
                padding-bottom: 0;
                &__container {
                    max-width: none;
                    padding: 0; }
                &__title {
                    margin-bottom: 15px;
                    position: relative; } } } } }

@media (max-width: 576px) {
    .header {
        &-project {
            padding: 15px 0;
            &__title {
                margin-bottom: 0;
                font-size: 36px; }
            &__text {
                font-size: 20px; }
            &s {
                &__title {
                    font-size: 24px; } } } } }
