.stages {
  &-block {
    // border: 1px solid black
    margin-bottom: 129px;
    position: relative;
    width: 280px;
    height: 352px;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    &:last-child {
      margin-bottom: 0; }
    &__svg {
      position: absolute;
      path {
        fill: none;
        stroke: $black;
        stroke-width: 2px; } }
    &s {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column; }
    &__title {
      height: 65px;
      display: flex;
      align-items: center;
      line-height: 1.4;
      text-align: center;
      text-transform: uppercase;
      font-size: 18px;
      color: $active;
      margin-bottom: 20px; }
    &__img {
      display: flex;
      justify-content: center;
      align-content: center;
      background: rgba($active, 0.7);
      width: 100px;
      height: 100px;
      border-radius: 50%;
      margin-bottom: 45px;
      img {
        width: 40px; } }
    &__text {
      width: 100%;
      padding-left: 50px;
      padding-right: 20px;
      justify-content: space-between;
      display: flex;
      align-items: center;
      font-size: 16px;
      position: relative;
      margin-bottom: 40px;
      height: 50px;
      span {
        left: 50%;
        position: absolute;
        display: inline-block;
        width: 2px;
        height: 50px;
        background: #000; } }

    &__button {
      clip-path: polygon(0% 85%, 0% 0%, 100% 0%, 100% 85%, 97% 100%, 3% 100%);
      width: 100%;
      height: 61px;
      border: none;
      background: $active;
      font-size: 18px;
      font-weight: 700;
      color: #fff; } } }


@media (min-width: 576px) {
  .stages {
    &-block {
      margin-bottom: 129px;
      width: 350px;
      height: 440px;
      &__title {
        font-size: 23px;
        margin-bottom: 26px; }
      &__img {
        width: 138px;
        height: 138px;
        img {
          width: 55px; } }
      &__text {
        padding-left: 50px;
        padding-right: 30px;
        font-size: 20px;
        margin-bottom: 50px;
        height: 52px;
        span {
          left: 50%;
          position: absolute;
          display: inline-block;
          width: 2px;
          height: 100%; } }

      &__button {
        transition: all 0.3s ease-out;
        height: 70px;
        font-size: 23;
        border: none;
        outline: none;
        &:hover {
          cursor: pointer;
          background-color: lighten($active, 7); } } } } }

@media (min-width: 1200px) {
  .stages {
    &-block {
      margin-bottom: 0;
      &s {
        flex-direction: row;
        justify-content: space-between; } } } }
