.main {
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  &-block {
    position: relative;
    padding: 15px 0;
    margin-bottom: 25px;
    &_wrapper {
      width: 100%; }
    &::before {
      z-index: -1;
      position: absolute;
      content: "";
      width: 97%;
      height: 100%;
      top: 0;
      left: 0;
      transition: all .5s ease-out;
      // transition-delay: .5s
      transform: translateX(-100%) skew(-20deg);
      opacity: 0;
      backdrop-filter: blur(5px);
      background: rgba(#fff, 0.4); }
    &_show {
      &::before {
        transform: translateX(-11%) skew(-20deg);
        opacity: 1; } } }
  &-bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
    img {
      object-fit: cover;
      object-position: 50% 50%;
      height: 100%;
      width: 100%; } }
  &-title {
    font-size: 23px;
    font-weight: 700;
    line-height: 29px;
    color: $black;
    margin-bottom: 5px;
    width: 90%;
    transform: translateX(-50%);
    opacity: 0;
    transition: all 1s ease-out; }
  &-subtitle {
    color: $black;
    font-size: 15px;
    width: 82%;
    transform: translateX(-50%);
    opacity: 0;
    transition: all 1s ease-out; }

  &-link {
    z-index: 1;
    padding: 2px 3px 2px 2px;
    color: $active;
    text-decoration: none;
    font-family: inherit;
    position: relative;
    display: inline-block;
    transition: color 0.2s ease-out;
    &::before {
      position: absolute;
      content: '';
      width: 100%;
      height: 1px;
      background: $active;
      left: 0;
      bottom: -2px;
      z-index: -1;
      transition: height 0.2s ease-out; }
    &:hover {
      color: $white;
      &::before {
        height: 100%; } } }
  &-button {
    display: block;
    transform: transitionX(-50%);
    opacity: 0;
    width: 132px;
    height: 40px;
    transition: all 1s ease-out; }

  &-scroll {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-tems: center;
    &:hover {
      cursor: pointer; }
    svg {
      width: 35px;
      height: 18px; }
    &__arrow {
      &_big {
        fill: $white; }
      &_small {
        fill: $white;
        fill-opacity: 0.5; } } } }
@-moz-document url-prefix() {
  .main-block {
    &::before {
      background: rgba(#fff, 0.7); } } }
@media (min-width: 576px) {
  .main {
    &-block {
      padding: 20px 0;
      margin-bottom: 30px;
      &::before {
        transform: translateX(-100%) skew(-30deg); }
      &_show {
        &::before {
          transform: translateX(-15%) skew(-30deg);
          opacity: 1; } } }
    &-title {
      width: 85%;
      font-size: 28px;
      line-height: 34px;
      margin-bottom: 10px; }
    &-subtitle {
      font-size: 18px;
      width: 80%; }
    &-button {
      width: 170px;
      height: 60px; }
    &-scroll {
      svg {
        width: 40px;
        height: 20px; } } } }

@media (min-width: 768px) {
  .main {
    &-block {
      padding: 25px 0;
      margin-bottom: 30px;
      &::before {
        transform: translateX(-100%) skew(-30deg); }
      &_show {
        &::before {
          transform: translateX(-30%) skew(-30deg);
          opacity: 1; } } }
    &-title {
      width: 70%;
      font-size: 30px;
      line-height: 34px;
      margin-bottom: 10px; }
    &-subtitle {
      font-size: 18px;
      width: 65%; } } }

@media (min-width: 992px) {
  .main {
    &-block {
      padding: 30px 0;
      margin-bottom: 30px;
      &::before {
        transform: translateX(-100%) skew(-30deg); }
      &_show {
        &::before {
          transform: translateX(-40%) skew(-30deg);
          opacity: 1; } } }
    &-title {
      width: 60%;
      font-size: 34px;
      line-height: 40px;
      margin-bottom: 15px; }
    &-subtitle {
      font-size: 18px;
      width: 55%; }
    &-button {
      width: 200px;
      height: 65px; }
    &-scroll {
      svg {
        width: 50px;
        height: 32px; } } } }

@media (min-width: 1200px) {
  .main {
    &-block {
      padding: 30px 0 35px;
      margin-bottom: 30px;
      &::before {
        transform: translateX(-100%) skew(-30deg); }
      &_show {
        &::before {
          transform: translateX(-40%) skew(-30deg);
          opacity: 1; } } }
    &-title {
      width: 63%;
      font-size: 40px;
      line-height: 48px;
      margin-bottom: 20px; }
    &-subtitle {
      font-size: 18px;
      width: 63%; }
    &-button {
      width: 200px;
      height: 65px; }
    &-scroll {
      svg {
        width: 50px;
        height: 32px; } } } }

@media (min-width: 1400px) {
  .main {
    &-block {
      padding: 50px 0 55px;
      margin-bottom: 50px;
      &::before {
        width: 77%; }
      &_show {
        &::before {
          transform: translateX(-10%) skew(-30deg);
          opacity: 1; } } }
    &-title {
      width: 77%;
      font-size: 50px;
      line-height: 71px;
      margin-bottom: 30px; }
    &-subtitle {
      font-size: 23px;
      width: 70%; }
    &-button {
      width: 276px;
      height: 90px; } } }
