* {
	box-sizing: border-box; }

h1, h2, h3, h4, h5, h6, ul {
	padding: 0;
	margin: 0; }
body {
	font-family: Montserrat, sans-serif; }
html {
	margin-right: calc(-1 * (100vw - 100%));
	overflow-x: hidden; }
img {
	max-width: 100%; }
.container {
	@include container(); }

.medium-zoom {
  &-image {
    z-index: 11; }
  &-overlay {
    z-index: 10; } }

.section {
	padding: 50px 0;
	background: #fff;
	&:nth-child(odd) {
		background: #f2f2f2; } }

.title {
	font-size: 18px;
	margin-bottom: 50px;
	text-align: center; }
@media (min-width: 576px) {
	.container {
		max-width: 540px; } }
@media (min-width: 768px) {
	.container {
		max-width: 730px; }
	.title {
		font-size: 24px; } }
@media (min-width: 990px) {
	.container {
		max-width: 960px; }
	.title {
		font-size: 26px; } }
@media ( min-width: 1200px) {
	.container {
		max-width: 1170px; }
	.section {
		padding: 100px 0; }
	.title {
		font-size: 30px;
		margin-bottom: 100px; } }
