.faq {
  &-list {
    list-style: none;
    &__element {
      z-index: 1;
      margin-bottom: 18px;
      position: relative;
      &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        transform: rotate(-90deg);
        transition: all 0.5s ease-out;
        content: '';
        background: url('../img/icons/arrow.svg') center no-repeat; }
      &_active {
        &::before {
          transform: rotate(0deg); }
        .faq-list__text_wrapper {
          width: 100%; } } }
    &__title {
      position: relative;
      z-index: 2;
      cursor: pointer;
      line-height: 21px;
      padding-left: 30px;
      font-size: 16px;
      font-weight: 600;
      margin-bottom: 15px; }
    &__images {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      img {
        z-index: 10;
        margin: 0 auto;
        max-width: 200px; } }
    &__text {
      width: 100%;
      position: relative;
      font-size: 16px;
      font-weight: 600;
      padding: 20px 30px 45px;
      border: 2px solid $black;

      &_wrapper {
        height: 2px;
        width: 0;
        display: flex;
        overflow: hidden;
        transition: all 0.5s ease-out; }
      &::before {
        content: '';
        position: absolute;
        width: 56px;
        height: 56px;
        right: -32px;
        bottom: -31px;
        border-left: 2px solid $black;
        transform: rotate(45deg);
        background: #f2f2f2; } } } }
@media (min-width: 1200px) {
  .faq {
    &-list {
      &__element {
        margin-bottom: 50px; }
      &__title {
        font-size: 23px; }
      &__text {
        font-size: 23px;
        padding: 40px 90px 40px 30px;
        &::before {
          width: 66px;
          height: 66px;
          right: -35px;
          bottom: -34px; } } } } }
