.svg-button {
  position: relative;
  svg {
    width: 100%;
    height: 100%; }
  path {
    fill: $active; }
  span {
    color: $white;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 700;
    &:hover {
      cursor: pointer; } } }


@media (min-width: 1200px) {
  .svg-button {
    span {
      font-size: 24px; } } }

@media (min-width: 1400px) {
  .svg-button {
    span {
      font-size: 30px; } } }
