.reqs {
    &-wrapper {
        overflow: hidden; }
    &-container {
        padding: 15px 30px;
        border: 2px solid $black;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            background: #f2f2f2;
            right: -21px;
            bottom: -23px;
            border-top: 2px solid $black;
            width: 40px;
            height: 40px;
            transform: rotate(-45deg);
            z-index: 2; } }
    &-text {
        display: flex;
        flex-direction: column;
        justify-content: space-between; }
    &-block {
        line-height: 1.2;
        font-weight: 600;
        font-size: 16px;
        margin-bottom: 10px;
        &__title {
            color: #a2a2a2; } }
    &-br {
        width: 100%;
        height: 2px;
        background: $black;
        margin-top: 20px;
        margin-bottom: 15px; }
    &-images {
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-wrap: wrap; }
    &-img {
        margin: 0 15px 30px;
        max-width: 220px;
        img {
            max-width: 100%; } } }
@media (min-width: 768px) {
    .reqs {
        &-container {
            padding: 30px 60px; }
        &-block {
            font-size: 18px; }
        &-br {
            margin-top: 35px;
            margin-bottom: 30px; } } }

@media (min-width: 990px) {
    .reqs {
        &-text {
            justify-content: space-between;
            flex-direction: row; }
        &-blocks {
            max-width: 45%; } } }

@media (min-width: 1200px) {
    .reqs {
        &-img {
            max-width: 225px; } } }

