.edge {
  .form-wrapper {
    background: #fff; }
  .form-bg {
    display: none; } }
.form {
  &-close {
    display: none;
    position: absolute;
    text-transform: uppercase;
    top: -70px;
    left: 50%;
    transform: translateX(-50%);
    color: $white;
    font-weight: 600;
    font-size: 10px;
    width: 60px;
    height: 60px;
    text-align: center;
    cursor: pointer;
    &::before, &::after {
      width: 45px;
      height: 2px;
      content: '';
      position: absolute;
      transform-origin: center center;
      background: $white;
      left: 50%;
      top: 37px; }
    &::before {
      transform: translateX(-50%) rotate(45deg); }
    &::after {
      transform: translateX(-50%) rotate(-45deg); } }
  &-success {
    &__text {
      font-size: 23px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: all 0.5s ease-out; }
    .form-success__text {
      opacity: 1 !important; }
    .form-wrapper {
      opacity: 0 !important; } }
  &-overlay {
    display: none;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 5;
    backdrop-filter: blur(5px);
    background: rgba($black, 0.5); }
  &-content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
  &-wrapper {
    opacity: 0;
    transition: all 0.5s ease-out;
    position: relative;
    padding: 30px 50px;
    width: 485px;
    height: 425px;
    color: $black; }
  &-title {
    font-size: 23px;
    text-align: center;
    font-weight: 600;
    margin-bottom: 24px; }
  &-bg {
    z-index: -1;
    width: 485px;
    height: 425px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  &-input {
    padding: 0 5px;
    font-size: 23px;
    width: 100%;
    border: none;
    &__fieldset {
      margin: 0;
      height: 100%;
      border: 2px solid $black;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        width: 30px;
        height: 30px;
        right: -19px;
        bottom: -19px;
        background: $white;
        transform: rotate(45deg);
        border: 2px solid $black; } }
    &__wrapper {
      overflow: hidden;
      height: 56px;
      width: 250px;
      margin: 0 auto 15px;
      legend {
        font-size: 10px;
        font-weight: 600;
        padding: 0 2px; } } }
  &-button {
    span {
      font-size: 23px !important; }
    width: 250px;
    height: 55px;
    margin: 0 auto; }
  &-small {
    margin-top: 20px;
    font-size: 10px;
    text-align: center;
    line-height: 1.5;
    a {
      z-index: 0;
      padding: 0 2px;
      color: $active;
      text-decoration: none;
      position: relative;
      transition: color 0.2s ease-out;
      &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 1px;
        background: $active;
        left: 0;
        z-index: -1;
        bottom: 0px;
        transition: height 0.2s ease-out; }
      &:hover {
        color: $white;
        &::before {
          height: 100%; } } } } }

@media (max-width: 768px) {
  .form {
    &-title {
      font-size: 17px; }
    &-success {
      &__text {
        font-size: 17px; } }
    &-input {
      font-size: 17px;
      &__wrapper {
        height: 45px; } }
    &-bg, &-wrapper {
      width: 450px;
      height: 370px; } } }
@media (max-width: 500px) {
  .form {
    &-title {
      font-size: 14px;
      margin-bottom: 12px; }
    &-input {
      font-size: 14px;
      &__wrapper {
        height: 40px;
        width: 200px;
        margin-bottom: 6px; }
      &__fieldset {
        padding-top: 2px;
        &::before {
          width: 20px;
          height: 20px;
          right: -14px;
          bottom: -14px; } } }
    &-small {
      line-height: 1.2;
      margin-top: 6px; }
    &-button {
      width: 200px;
      span {
        font-size: 14px !important; } }
    &-bg, &-wrapper {
      width: 300px;
      height: 260px; }
    &-wrapper {
      padding: 10px; } } }
