.rangeslider {
  display: block;
  margin-top: 25px;
  position: relative;
  &--horizontal {
    width: 100%;
    height: 8px;
    background: none;
    border: 2px solid $black; }
  &__fill {
    top: 0;
    height: 100%;
    position: absolute;
    display: block;
    background: rgba($active, 0.5); }
  &__handle {
    z-index: 2;
    position: absolute;
    width: 10px;
    height: 25px;
    background: $active;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    touch-action: pan-y;
    clip-path: polygon(0% 0%, 100% 0%, 100% 85%, 0% 100%); } }

@media ( min-width: 1200px ) {
  .rangeslider {
    margin-top: 30px;
    &--horizontal {
      height: 10px; }
    &__handle {
      width: 18px;
      height: 43px; } } }
