.edge {
  .header-menu__overlay {
    background: #fff; } }
.header {
  width: 100vw;
  position: fixed;
  backdrop-filter: blur(5px);
  background: rgba(#fff, 0.4);
  padding: 15px 0;
  z-index: 5;
  opacity: 0;
  transform: translateY(-100%);
  transition: all 0.5s ease-out;
  &_active {
    .header-contacts {
      li {
        opacity: 1; } }
    .header-menu {
      &__list {
        ul {
          li {
            opacity: 1; } } }
      &__link {
        &::after {
          opacity: 1; } }
      &__button {
        span {
          opacity: 0; }
        &::before {
          left: 0;
          top: 50%;
          transform: rotate(135deg) skew(-45deg); }
        &::after {
          left: 0;
          top: 50%;
          transform: rotate(-135deg) skew(-45deg); } } } }
  &-content {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      cursor: pointer; }
    &__image {
      margin-right: 10px;
      width: 40px; }
    &__text {
      font-size: 15px;
      line-height: 17px;
      font-family: "AMG Anahit Semi Serif"; } }
  &-contacts {
    list-style: none;
    display: flex;
    align-items: center;
    li {
      opacity: 0;
      transition: all 0.2s ease-out;
      &:nth-child(1) {
        transition-delay: 3.4s; }
      &:nth-child(2) {
        transition-delay: 3.8s; }
      &:nth-child(3) {
        transition-delay: 4.2s; } }

    &__item {
      text-decoration: none;
      color: inherit;
      font-weight: 600;
      font-family: inherit;
      margin: 0 5px;
      display: flex;
      align-items: center;
      transition: all 0.2s ease-out;
      &:hover {
        .svg {
          fill: $active; }
        color: $active; }
      span {
        display: none;
        font-size: 14px;
        margin-left: 10px; } }
    .svg {
      transition: all 0.2s ease-out;
      width:  25px;
      height: 20px; } }
  &-menu {
    &__list {
      position: relative;
      z-index: 5;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 100%;
      ul {
        list-style: none;
        li {
          opacity: 0;
          transition: all 0.2s ease-out;
          margin: 30px 0;
          &:nth-child(1) {
            transition-delay: 1.4s; }
          &:nth-child(2) {
            transition-delay: 1.8s; }
          &:nth-child(3) {
            transition-delay: 2.2s; }
          &:nth-child(4) {
            transition-delay: 2.6s; }
          &:nth-child(5) {
            transition-delay: 3s; } } } }
    &__link {
      cursor: pointer;
      font-size: 18px;
      color: $black;
      text-decoration: none;
      position: relative;
      transition: color 0.2s ease-out;
      padding: 0 5px;
      font-weight: 700;
      &_active {
        &::after {
          width: 50px;
          content: '';
          height: 4px;
          background: $black;
          position: absolute;
          top: 50%;
          left: calc(100% + 100px);
          opacity: 0;
          transition-delay: 5s;
          transform: translate(-50%, -50%);
          animation: move 0.2s forwards ease-in 5.4s; } }
      &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 0px;
        background: $active;
        left: 0;
        bottom: -1px;
        z-index: -1;
        transition: height 0.2s ease-out; }
      &:hover {
        color: $white;
        &::before {
          height: 100%; } } }
    &__bg {
      position: absolute;
      top: 0;
      transform-origin: center center;
      left: 0; }
    &__overlay {
      display: none;
      position: fixed;
      z-index: 4;
      width: 100vw;
      height: 100vh;
      top: 0;
      left: 0;
      overflow: hidden; }

    &__button {
      width: 35px;
      height: 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      position: relative;
      span {
        transition: all 0.3s ease-out;
        position: relative;
        display: block;
        background: #000000;
        height: 2px;
        width: 100%;
        transform: skew(-45deg); }
      &::before, &::after {
        transform: skew(-45deg);
        transition: all 0.3s ease-out;
        position: absolute;
        content: '';
        background: #000000;
        height: 2px;
        width: 100%; }
      &::before {
        top: 25%; }
      &::after {
        top: 70%; } }
    &:hover {
      cursor: pointer; } } }

@-moz-document url-prefix() {
  .header {
    background: rgba(#fff, 0.95); } }

@media (min-width: 400px) {
  .header {
    &-contacts {
      &__item {
        margin: 0 15px; } } } }
@media (min-width: 500px) {
  .header {
    &-contacts {
      &__item {
        span {
          display: block; } } }
    &-menu {
      &__link {
        font-size: 20px; } } } }

@media (min-width: 768px) {
  .header {
    &-contacts {
      &__item {
        margin: 0 25px;
        span {
          font-size: 16px; } } }
    &-menu {
      &__link {
        font-size: 30px; } } } }
@media (min-width: 992px) {
  .header {
    padding: 15px 0px;
    &-logo {
      &__image {
        width: 45px; }
      &__text {
        font-size: 18px;
        line-height: 20px; } }
    &-contacts {
      &__item {
        margin: 0 25px;
        cursor: pointer;
        span {
          font-size: 18px; } } }
    &-menu {
      &__list {
        ul {
          li {
            margin: 50px 0; } } }
      &__link {
        font-size: 40px; }
      &__button {
        width: 40px;
        span {
          &::before {
            top: -9px;
            left: -9px; }
          &::after {
            top: 9px;
            left: 9px; } } } } } }
@media (min-width: 1200px) {
  .header {
    &-menu {
      &__link {
        font-size: 50px; } } } }
@media (min-width: 1400px) {
  .header {
    padding: 22px 0px;
    &-logo {
      &__image {
        width: 56px; }
      &__text {
        font-size: 23px;
        line-height: 23px; } }
    &-menu {
      &__button {
        width: 50px;
        span {
          &::before {
            top: -9px;
            left: -9px; }
          &::after {
            top: 9px;
            left: 9px; } } } } } }

@keyframes move {
  0% {
    left: calc(100% + 100px); }
  100% {
    left: -40px; } }
