.footer {
  background: #4d4d4d;
  padding-top: 50px;
  color: $white;
  overflow: hidden;
  &_wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column; }
  &-title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 20px; }
  &-contacts {
    margin-bottom: 30px; }
  ul {
    list-style: none; }
  &-item {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 20px;
    font-size: 16px;
    font-weight: 600; }
  &-link {
    display: block;
    margin-bottom: 5px;
    color: $white;
    transition: all 0.2s ease-out;
    text-decoration: none;
    font-weight: 600;
    &:hover {
      color: $active; } }
  .svg {
    min-width: 25px;
    max-width: 25px;
    fill: #fff;
    height: 20px;
    margin-right: 10px; }
  &-small {
    text-align: center;
    font-size: 14px;
    line-height: 40px; }
  &-map {
    width: 100vw;
    height: 300px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%); }
  &-adress {
    width: 100%;
    position: relative;
    margin-bottom: 300px;
    .footer-item {
      flex-wrap: wrap; }
    .svg {
      min-width: 15px; }
    span {
      display: block; } } }
@media (min-width: 768px) {
  .footer {
    &_wrapper {
      flex-direction: row;
      justify-content: space-between; }
    &-adress {
      max-width: 50%;
      margin-bottom: 350px; }
    &-map {
      margin-top: 50px;
      left: 0; } } }

@media (min-width: 990px) {
  .footer {
    &-title {
      font-size: 23px;
      margin-bottom: 25px; }
    &-item {
      margin-bottom: 25px;
      font-size: 18px;
      .svg {
        height: 22px;
        min-width: 30px;
        max-width: 30px; } }
    &_wrapper {
      flex-direction: row;
      justify-content: space-between; }
    &-adress {
      max-width: 50%;
      margin-bottom: 400px; }
    &-map {
      margin-top: 100px;
      left: 0; } } }

@media (min-width: 1200px) {
  .footer {
    padding-top: 100px;
    padding-bottom: 50px;
    &-title {
      font-size: 25px;
      margin-bottom: 50px; }
    &-item {
      margin-bottom: 50px;
      font-size: 23px;
      .svg {
        height: 25px;
        min-width: 30px;
        max-width: 30px; } }
    &-link {
      margin-bottom: 10px; }
    &_wrapper {
      flex-direction: row;
      justify-content: space-between; }
    &-adress {
      margin-bottom: 415px;
      max-width: 50%; }
    &-map {
      margin-top: 0;
      width: calc(50vw - 9px);
      top: auto;
      left: 0;
      height: 445px;
      transform: translateX(0); }
    &-small {
      line-height: auto;
      text-align: left;
      font-size: 23px; } } }
