.table {
  overflow: hidden;
  &_wrapper {
    overflow-x: auto;
    overflow-y: hidden;
    &_w {
      overflow: hidden;
      width: 850px; } }
  table {
    margin: 0 auto;
    position: relative;
    width: 850px;
    border-collapse: collapse;
    td, th {
      width: 25%;
      font-size: 15px;
      text-align: center;
      line-height: 60px;
      border: 2px solid $black;
      &:nth-child(2) {
        background: rgba($active, 0.3);
        font-weight: 400; } }
    th {
      border-bottom: none; }
    td:nth-child(1) {
      font-weight: 700; }
    &::before, &::after {
      background: #f2f2f2;
      content: '';
      position: absolute;
      width: 30px;
      height: 30px; }
    &::before {
      transform: rotate(45deg);
      top: -15px;
      left: -16px;
      border-right: 2px solid $black; }
    &::after {
      transform: rotate(45deg);
      bottom: -15px;
      right: -16px;
      border-left: 2px solid $black; }
    span {
      vertical-align: middle;
      font-family: "AMG Anahit Semi Serif"; }
    img {
      vertical-align: middle;
      width: 40px;
      margin-right: 10px; } } }

@media (min-width: 1200px) {
  .table {
    &_wrapper {
      &_w {
        width: 1140px; } }
    table {
      width: 1140px;
      td, th {
        width: 25%;
        font-size: 21px;
        line-height: 100px;
        &:nth-child(2) {
          background: rgba($active, 0.3); } }
      &::before, &::after {
        z-index: 2;
        width: 60px;
        height: 60px; }
      &::before {
        top: -30px;
        left: -30px;
        border-right: 2px solid $black; }
      &::after {
        bottom: -30px;
        right: -30px;
        border-left: 2px solid $black; }
      img {
        width: 50px;
        margin-right: 10px; } } } }
