.calc {
  &-cap {
    font-size: 14px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    &_disabled {
      opacity: 0.5; }
    span {
      position: absolute;
      width: 18px;
      height: 18px;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      &::before {
        left: 0;
        position: absolute;
        content: '';
        top: 50%;
        transform: translateY(-50%);
        height: 16px;
        width: 16px;
        background: none;
        border: 2px solid $black;
        cursor: pointer; }
      &::after {
        left: 0;
        cursor: pointer;
        position: absolute;
        content: '';
        top: 50%;
        transform: translateY(-50%);
        left: 6px;
        height: 8px;
        width: 8px; } }
    &_checked {
      span {
        &::after {
          background: $active; } } }
    input {
      cursor: pointer;
      margin-right: 10px;
      opacity: 0;
      width: 20px;
      height: 20px; }
    label {
      cursor: pointer;
      position: relative;
      display: flex;
      flex-direction: row-reverse;
      align-items: center; } }

  &-input {
    font-weight: 600;
    font-size: 16px;
    display: block;
    margin-bottom: 15px;
    line-height: 28px;
    position: relative;
    &__range {
      display: flex;
      justify-content: space-between;
      align-items: center; }
    &__wrapper {
      position: relative;
      margin-bottom: 30px; }
    &__results {
      position: absolute;
      right: 0;
      top: 0;
      font-size: 16px;
      width: 125px;
      text-align: center;
      border: 2px solid $black;
      font-weight: 600;
      &::before {
        background: $white;
        content: '';
        position: absolute;
        width: 12px;
        height: 12px;
        transform: rotate(45deg);
        right: -8px;
        bottom: -7px;
        border-left: 2px solid $black; } } }
  &-results {
    position: relative;
    padding: 35px;
    border: 2px solid $black;
    margin-bottom: 30px;
    &_wrapper {
      overflow: hidden; }
    &__block {
      font-size: 14px;
      display: flex;
      justify-content: space-between;
      align-content: center;
      margin-bottom: 35px;
      &:last-child {
        margin-bottom: 0; } }
    &::before {
      background: $white;
      content: '';
      position: absolute;
      width: 46px;
      height: 46px;
      transform: rotate(45deg);
      right: -25px;
      bottom: -24px;
      border-left: 2px solid $black; }
    &__title {
      font-weight: 600; } }
  &-button {
    margin: 0 auto;
    width: 220px;
    height: 40px; }
  &-rect {
    position: absolute;
    display: block;
    width: 2px;
    background: $black;
    height: 6px;
    bottom: -6px;
    left: 0;
    z-index: 1;
    &_long {
      height: 12px;
      bottom: -12px; }
    &__term {
      &:nth-child(1) {
        left: 0; }
      &:nth-child(7) {
        left: 20%; }
      &:nth-child(3) {
        left: 40%; }
      &:nth-child(4) {
        left: 60%; }
      &:nth-child(5) {
        left: 80%; }
      &:nth-child(6) {
        left: calc(100% - 2px); } }
    &__sum {
      &:nth-child(1) {
        left: 0; }
      &:nth-child(6) {
        left: 25%; }
      &:nth-child(5) {
        left: 50%; }
      &:nth-child(4) {
        left: 75%; }
      &:nth-child(3) {
        left: calc(100% - 2px); } } } }


@media (min-width: 460px) {
  .calc {
    &-results {
      margin-left: auto;
      margin-right: auto;
      width: 70%;
      &__block {
        font-size: 16px; } } } }

@media (min-width: 768px) {
  .calc {
    &-form {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around; }
    &-input {
      &__wrapper {
        width: 45%; } } } }

@media (min-width: 991px) {
  .calc {
    &-input {
      margin-bottom: 20px;
      font-size: 18px;
      &__wrapper {
        margin-bottom: 40px; }
      &__range {
        font-size: 18px; }
      &__results {
        font-size: 18px;
        padding: 2px; } }
    &-cap {
      font-size: 18px;
      input {
        margin-right: 12px;
        width: 26px;
        height: 26px; }
      span {
        width: 26px;
        height: 26px;
        &::before {
          width: 24px;
          height: 24px; }
        &::after {
          left: 8px;
          width: 12px;
          height: 12px; } } } } }
@media (min-width: 1200px) {
  .calc {
    &-cap {
      font-size: 23px;
      justify-content: flex-start;
      margin-bottom: 0;
      input {
        margin-right: 12px;
        width: 32px;
        height: 32px; }
      span {
        width: 32px;
        height: 32px;
        &::before {
          width: 28px;
          height: 28px; }
        &::after {
          left: 9px;
          width: 14px;
          height: 14px; } } }

    &_wrapper {
      display: flex;
      justify-content: space-between;
      align-items: flex-start; }
    &-results {
      width: 100%;
      margin: 0;
      padding: 50px;
      margin-bottom: 60px;
      &_wrapper {
        width: 100%;
        max-width: 37%; }
      &__block {
        font-size: 23px;
        margin-bottom: 50px; } }
    &-button {
      margin: 0;
      width: 380px;
      height: 70px; }
    &-form {
      width: 100%;
      margin-right: 50px;
      display: block; }
    &-input {
      font-size: 23px;
      margin-bottom: 30px;
      &__range {
        font-size: 23px; }
      &__wrapper {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 65px; }
      &__results {
        min-width: 200px;
        position: relative;
        font-size: 23px;
        line-height: 50px;
        &::before {
          width: 16px;
          height: 16px;
          right: -10px;
          bottom: -9px; } }
      &__block {
        width: 100%;
        margin-right: 50px; } }
    &-rect {
      height: 10px;
      bottom: -10px;
      &_long {
        height: 20px;
        bottom: -20px; } } } }
