.reviews {
  &-slide {
    border: 2px solid $black;
    position: relative;
    padding: 10px 40px 10px 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    &_wrapper {
      overflow: hidden; }
    &::before {
      position: absolute;
      content: '';
      width: 50px;
      height: 50px;
      right: -27px;
      bottom: -26px;
      transform: rotate(45deg);
      border-left: 2px solid $black;
      background: $white; }
    &::after {
      right: 10px;
      top: -10px;
      content: '';
      font-size: 157px;
      position: absolute;
      opacity: 0.05;
      font-weight: 600; }
    &__img {
      object-fit: cover;
      border-radius: 50%;
      width: 50px;
      height: 50px;
      margin-right: 18px; }
    &__name {
      font-size: 16px;
      font-weight: 600; }
    &__text {
      width: 100%;
      margin-top: 10px;
      font-size: 16px;
      margin-bottom: 15px; }
    &__date {
      width: 100%;
      text-align: right;
      font-weight: 600; } } }

.slick {
  &-dots {
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    height: 70px;
    li {
      margin: 0 5px;
      button {
        font-size: 0;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background: rgba($black, 0.3);
        border: none;
        cursor: pointer;
        outline: none; } }
    .slick-active {
      button {
        background: $active; } } }

  &-arrow {
    cursor: pointer;
    background: url('../img/reviews/arrow.png') center no-repeat;
    font-size: 0;
    width: 25px;
    height: 64px;
    border: none;
    position: absolute;
    bottom: 0;
    outline: none; }
  &-next {
    transform: rotate(180deg);
    right: 0; }
  &-prev {
    left: 0; } }

@media (min-width: 576px) {
  .reviews {
    &-slide {
      &__text, &__name, &__date {
        font-size: 18px; } } } }

@media (min-width: 768px) {
  .reviews {
    &-slide {
      padding: 25px 45px 25px 25px; } } }

@media (min-width: 991px) {
  .reviews {
    padding-bottom: 100px;
    &-slide {
      padding: 40px 50px 40px 210px;
      flex-direction: column;
      align-items: flex-start;
      &r {
        padding: 0 30px; }
      &_wrapper {
        padding: 0px 30px; }
      &::after {
        font-size: 265px;
        right: auto;
        left: 233px;
        top: 20px; }
      &::before {
        width: 66px;
        height: 66px;
        right: -35px;
        bottom: -34px; }
      &__img {
        width: 138px;
        position: absolute;
        height: 138px;
        left: 45px;
        top: 35px; }
      &__name {
        font-size: 20px; }
      &__text {
        margin-top: 15px;
        margin-bottom: 15px;
        font-size: 20px; }
      &__date {
        font-size: 20px; } } }
  .slick-dots {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 90%); }
  .slick-arrow {
    top: 50%;
    transform: translateY(-50%); }
  .slick-next {
    transform: translateY(-50%) rotate(180deg); } }

@media (min-width: 1200px) {
  .reviews {
    padding-bottom: 135px;
    &-slide {
      padding: 45px 50px 45px 230px;
      flex-direction: column;
      align-items: flex-start;
      &r {
        padding: 0 30px; }
      &_wrapper {
        padding: 0px 30px; }
      &::after {
        font-size: 265px;
        right: auto;
        left: 233px;
        top: 50px; }
      &::before {
        width: 66px;
        height: 66px;
        right: -35px;
        bottom: -34px; }
      &__img {
        width: 138px;
        position: absolute;
        left: 45px;
        top: 35px;
        height: 138px; }
      &__name {
        font-size: 23px; }
      &__text {
        margin-top: 45px;
        margin-bottom: 40px;
        font-size: 23px; }
      &__date {
        font-size: 23px; } } }
  .slick-dots {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 90%); }
  .slick-arrow {
    top: 50%;
    transform: translateY(-50%); }
  .slick-next {
    transform: translateY(-50%) rotate(180deg); } }
